import React, { useEffect, useState } from 'react';
import Menu from './Menu';
import Busqueda from './Busqueda';
import axios from 'axios';

const url = 'https://civico.cl/menu/';

let axiosConfig = {
  headers: {
    'Content-Type': 'application/json;charset=UTF-8',
    "Access-Control-Allow-Origin": "*",
  }
};


function App() {
  const [productos, setProductos] = useState([]);
  const [isLoading, setLoading] = useState(true);

  useEffect(() => {
    const obtenerProductos = async () => {
      const respuesta = await axios.get(url, axiosConfig);
      setProductos(respuesta.data);
      setLoading(false);
    }

    obtenerProductos();


  }, []);

  const categorias = [...new Set(productos.map((item) => item.nombre_familia))];
  const todos = [], bebestibles = [], comidas = [];

  categorias.forEach(function (nombre) {
    const x = productos.filter((item) => item.nombre_familia === nombre);
    if (x[0].superfamilia === "Comida") {
      comidas.push(x);
    }
    else {
      bebestibles.push(x);
    }
    todos.push(x);
  });


  const [menuItems, setMenuItems] = useState(todos);
  const [aux, setAux] = useState(0);
  const [activeCategory, setActiveCategory] = useState("");
  const [categoriaSeleccionada, setCategoriaSeleccionada] = useState('Bebestibles');

  const filterItems = (category) => {
    setAux(1);
    setActiveCategory(category);
    setCategoriaSeleccionada(`${category}`);
    if (category === "Todos") {
      setMenuItems(todos);
    }
    else if (category === "Comida") {
      setMenuItems(comidas);
    }
    else {
      setMenuItems(bebestibles);
    }
  };


  const [estadoBuscar, setEstadoBuscar] = useState(false);
  const [searchText, setSearchText] = useState('');
  const [results, setResults] = useState([]);
  function removeAccents(text) {
    return text.normalize("NFD").replace(/[\u0300-\u036f]/g, "");
  }

  function includesAdvanced(originalString, searchString) {
    const normalizedOriginalString = removeAccents(originalString.toLowerCase());
    const normalizedSearchString = removeAccents(searchString.toLowerCase());

    const originalWords = normalizedOriginalString.split(/\s+/);
    const searchWords = normalizedSearchString.split(/\s+/);

    for (const searchWord of searchWords) {
      if (originalWords.some((word) => word.includes(searchWord))) {
        return true;
      }
    }

    return false;
  }
  // Función para manejar la búsqueda

  const handleSearch = (event) => {
    if (estadoBuscar) {
      const searchText = event.target.value.toLowerCase();
      setSearchText(searchText);


      // Realizar la lógica de búsqueda aquí (puedes usar una API o un arreglo de datos)
      const filteredResults = [];

      for (let i = 0; i < bebestibles.length; i++) {
        for (let k = 0; k < bebestibles[i].length; k++) {
          if (
            includesAdvanced(
              bebestibles[i][k].nombre.toLowerCase().trim(),
              searchText.trim()
            ) ||
            includesAdvanced(
              bebestibles[i][k].descripcion.toLowerCase().trim(),
              searchText.trim()
            )
          ) {
            filteredResults.push(bebestibles[i][k]);
          }
        }
      }

      filteredResults.sort((a, b) => {
        const aMatches =
          (includesAdvanced(a.nombre.toLowerCase().trim(), searchText.trim()) ? 1 : 0) +
          (includesAdvanced(a.descripcion.toLowerCase().trim(), searchText.trim()) ? 1 : 0) +
          (a.nombre.includes(searchText.trim) ? 2 : 0);
        const bMatches =
          (includesAdvanced(b.nombre.toLowerCase().trim(), searchText.trim()) ? 1 : 0) +
          (includesAdvanced(b.descripcion.toLowerCase().trim(), searchText.trim()) ? 1 : 0) +
          (a.nombre.includes(searchText.trim) ? 2 : 0);
        return bMatches - aMatches;
      });

      setResults(filteredResults);
    }
  };

  const handleClick = () => {
    setEstadoBuscar(true);
  }
  const handleBlur = () => {
    setSearchText('');
    setEstadoBuscar(false);
  };



  return isLoading ? (
    <></>
  ) : (
    <div className="menuGrande">
      <div className="vintage-container">
        <div className='menu-card'>
          <div className="cafe-main-image-brand">
            <img className="header__logo" alt="logo" src={require('./civico.webp')} />
          </div>
        </div>
        <div className="menu">
          <div className="container">
            <div className="indicators">
              <div id="i2">
                <div className={activeCategory === "" || activeCategory === "Bebestibles" ? "navi-indicator1" : "navi-indicator1-inactive"} id={activeCategory === "" || activeCategory === "Bebestibles" ? "ni1" : "ni1-inactive"}></div>
              </div>
              <div id="i3">
                <div className={activeCategory === "Comida" ? "navi-indicator2" : "navi-indicator1-inactive"} id={activeCategory === "Comida" ? "ni2" : "ni2-inactive"}></div>
              </div>
            </div>
            <div className="navi">
              <div className="navi-item2">
                <button className={activeCategory === "" || activeCategory === "Bebestibles" ? "nav-button active" : "nav-button"} id="startersbutton" onClick={() => filterItems("Bebestibles")}>
                  <div className="navi-icon">
                    <span className="custom-icon2" role="img" aria-labelledby="comida"></span>
                  </div>
                </button>
              </div>
              <div className="navi-item3">
                <button className={activeCategory === "Comida" ? "nav-button active" : "nav-button"} id="mainsbutton" onClick={() => filterItems("Comida")}>
                  <div className="navi-icon">
                    <span className="custom-icon1" role="img" aria-labelledby="comida"></span>
                  </div>
                </button>
              </div>
            </div>
          </div>

          <div className="categoria-seleccionada">
            <h1 >{categoriaSeleccionada}</h1>
          </div>

          {!estadoBuscar || !searchText.length > 0 ? aux === 0 ?
            bebestibles.map((categoria, id) =>
              <Menu key={id} items={categoria} i={id}></Menu>
            )
            :
            menuItems.map((categoria, id) =>
              <Menu key={id} items={categoria} i={id}></Menu>
            )
            :
            results.length > 0 ?
              results.map((categoria, id) =>
                <Busqueda key={id} items={categoria} i={id}></Busqueda>
              )
              :
              <div className="error">
                <h2>No hay resultados</h2>
              </div>
          }
          <div className="contenido-anterior">
          </div>
          <div className="sticky-footer" >
            <div className="buscar">
              <input
                type="text"
                placeholder="Buscar"
                autoComplete='off'
                value={searchText}
                onChange={handleSearch}
                onBlur={handleBlur}
                onClick={handleClick}
              />
              <div className="search"></div>
            </div>
          </div>
        </div >
      </div >
    </div>
  );
}
export default App;