import React from 'react';
import { BrowserRouter, Routes, Route } from 'react-router-dom'
import Principal from './Principal';

function App() {
  return (
    <div className="App">
      <BrowserRouter>
        <Routes>
          <Route path='/yemo' element={<Principal></Principal>}></Route>
        </Routes>
      </BrowserRouter>
    </div>
  )
}
export default App;
