import React from 'react';
const Busqueda = ({ items }) => {
    function numberWithCommas(x) {
        var parts = x.toString().split(".");
        parts[0] = parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ".");
        return parts.join(".");
    }
    return (
        <div>
            {items.nombre != null ?
                <div className="items2">
                    <div key={items.id}>
                        <div className="precio-nombre">
                            <h2 className="nombre-item">{items.nombre}</h2>
                            <span></span>
                            <h3 className="precio">${numberWithCommas(items.precio)}</h3>
                        </div>
                        <p className="item-description">{items.descripcion}</p>
                    </div>
                </div>
                :
                <div>
                    <h1>No se encuentra el producto</h1>
                </div>}

        </div >
    );
};

export default Busqueda;
